// Swiper Optimizado
//
// Imágenes de todas las propiedades 'promo:' de Playlist o Canciones
// Con 3 categorías para priorizar el loop aleatorio
// Con redirección a la ruta de la playlist.
// Evita repetir el mismo

import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import { EffectCoverflow, Autoplay } from 'swiper/modules';
import { PLAYLIST } from '../../data/index';
import 'boxicons';
import { useNavigate } from 'react-router-dom';
import styles from './promote-uno.module.css';

const PromoteUno = ({ initialIndex }) => {
  const navigate = useNavigate();
  const [shuffledPromos, setShuffledPromos] = useState([]);

  // Uso de useEffect para calcular las promos
  useEffect(() => {
    const allPromos = (PLAYLIST || []).flatMap(list => {
      const playlistPromo = list.promo
        ? [{ image: list.promo, category: list.promoCategory || 3, link: list.link }]
        : [];

      const songPromos = (list.playlistData || [])
        .filter(song => song.promo)
        .map(song => ({
          image: song.promo,
          category: song.promoCategory || 3,
          link: list.link
        }));

      return [...playlistPromo, ...songPromos];
    });

    const priorityPromos = allPromos.flatMap(promo => {
      if (promo.category === 1) return [promo, promo, promo];
      if (promo.category === 2) return [promo, promo];
      return [promo];
    });

    // Mezclar las promociones para randomizar el orden
    const shuffled = priorityPromos.sort(() => Math.random() - 0.5);
    setShuffledPromos(shuffled);
  }, []);

  const [currentIndex, setCurrentIndex] = useState(initialIndex || 0);
  
  const handleImageClick = (link) => {
    // Redirigir a la ruta de la playlist
    navigate(`/playlist/${link}`);
  };

  return (
    <div className={styles.Container}>
      <div className={styles.ContentContainer}>
        {shuffledPromos.length > 0 && (
          <Swiper  
            effect={'coverflow'}
            grabCursor={true}
            centeredSlides={true}
            slidesPerView={'auto'} // Ajuste automático
            loop={shuffledPromos.length > 1} // Activar loop solo si hay más de una diapositiva
            speed={1000}
            autoplay={{
              delay: 6000,
              disableOnInteraction: false,
            }}
            coverflowEffect={{
              rotate: 0,
              stretch: 20,
              depth: 250,
              modifier: 1,
              slideShadows: false,
            }}
            modules={[EffectCoverflow, Autoplay]}
            className="mySwiper"
          >
            {shuffledPromos.map((promo, idx) => (
              <SwiperSlide 
                key={idx} 
                className={styles.SwiperSlide}
                onClick={() => handleImageClick(promo.link)} // Manejar clic en la imagen
              >
                <img src={promo.image} alt={`Promo ${idx}`} className={styles.ImageHD} loading="lazy" />
              </SwiperSlide>
            ))}
          </Swiper>
        )}
      </div>
    </div>
  );
};

export default PromoteUno;