import React from 'react';
import { Link } from 'react-router-dom';
import styles from './banner-link.module.css';

const BannerLink = ({ image, text, subtitle, description, to }) => {
  return (
    <div className={styles.bannerLink}>
      <Link to={to}>
        <img src={image} alt="Banner" />
        <div className={styles.bannerText}>{text}</div>
        <div className={styles.gridPromote}>
          <div className={styles.bannerSubtitle}>{subtitle}</div>
          <div className={styles.bannerDescription}>{description}</div>
        </div>
      </Link>
    </div>
  );
};

export default BannerLink;