import React, { useState, useEffect } from "react";
import { connect } from 'react-redux';
import { changeTrack } from '../../actions';
import { Link } from "react-router-dom";
import TextBoldL from '../text/text-bold-l';
import PlayButton from '../buttons/play-button';
import ContentLoader from "react-content-loader";
import { PLAYLIST } from '../../data/index';

import styles from "./playlist-card-s.module.css";

// Utilidad para convertir el tiempo en formato "min:sec" a segundos
const timeToSeconds = (time) => {
  const [minutes, seconds] = time.split(':').map(Number);
  return (minutes || 0) * 60 + (seconds || 0);
};

// Función para sumar los tiempos de las pistas y redondear a minutos
const sumTrackTimes = (tracks) => {
  const totalSeconds = tracks.reduce((total, track) => {
    const trackTime = track.trackTime || '0:00'; // Asegúrate de manejar valores indefinidos
    return total + timeToSeconds(trackTime);
  }, 0);
  
  const totalMinutes = Math.round(totalSeconds / 60); // Redondear a minutos

  // Formatear los minutos
  return `${totalMinutes}m`;
};

function PlaylistCardS(props) {
    const [loaded, setLoaded] = useState(false);

    useEffect(() => {
        // Comprobar si esta lista de reproducción es la actual en reproducción
        if (parseInt(props.data.index) === props.trackData.trackKey[0]) {
            setLoaded(true);
        }
    }, [props.trackData.trackKey, props.data.index]);

    function changeTheme() {
        document.documentElement.style.setProperty('--hover-home-bg', props.data.hoverColor);
    }

    function handleClick() {
        props.changeTrack([parseInt(props.data.index), 0]);
    }

    function handleImageLoad() {
        setLoaded(true);
    }

    function handleError(e) {
        e.target.src = './../image/set/selection.jpg';
    }

    // Filtrar los datos de la lista de reproducción actual
    const playlistData = PLAYLIST.find(playlist => playlist.link === props.data.link);
    const totalPlaylistTime = playlistData ? sumTrackTimes(playlistData.playlistData) : '0m';

    // Determinar si esta lista de reproducción es la actual y se está reproduciendo
    const isThisPlaylistActive = parseInt(props.data.index) === props.trackData.trackKey[0];

    return (
        <div className={styles.PlaylistCardSBox}>
            <Link to={`/playlist/${props.data.link}`} onMouseOver={changeTheme}>
                <div className={styles.PlaylistCardS}>
                    <div className={styles.ImgBox}>
                        {!loaded && <ContentLoader 
                            speed={4}
                            width={140}
                            height={210}
                            viewBox="0 0 200 300"
                            backgroundColor="rgba(30,30,30,.7)"
                            foregroundColor="rgba(50,50,50,.5)">
                            <rect x="0" y="0" rx="12" ry="12" width="200" height="300" />
                        </ContentLoader>}
                        <img 
                            src={props.data.imgUrl} 
                            alt={`${props.data.title}`} 
                            onError={handleError} 
                            onLoad={handleImageLoad} 
                            style={loaded ? {} : { display: 'none' }} 
                        />
                    </div>
                    <div className={styles.Title}>
                        <TextBoldL>{props.data.title}</TextBoldL>
                        <b className={styles.Count}>{totalPlaylistTime}</b> 
                    </div>
                </div>
            </Link>
            <div
                onClick={handleClick} 
                className={`${styles.IconBox} ${isThisPlaylistActive && props.isPlaying ? styles.ActiveIconBox : ''}`}
            >
                <PlayButton isthisplay={isThisPlaylistActive} />
            </div>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        trackData: state.trackData,
        isPlaying: state.isPlaying
    };
};

export default connect(mapStateToProps, { changeTrack })(PlaylistCardS);