import React, { useState, useEffect } from "react";
import { connect } from 'react-redux';
import { changeTrack } from '../../actions';
import { Link } from "react-router-dom";
import TextBoldL from '../text/text-bold-l';
import PlayButton from '../buttons/play-button';
import { useSwipeable } from 'react-swipeable';
import Marquee from 'react-fast-marquee';
import ContentLoader from "react-content-loader";

import styles from "./playlist-card-g.module.css";

function PlaylistCardG(props) {
    const [isthisplay, setIsthisPlay] = useState(false);
    const [currentImageIndex, setCurrentImageIndex] = useState(0);
    const [fade, setFade] = useState(false);
    const [loadedImages, setLoadedImages] = useState([]);

    const images = [props.data.imgUrl, props.data.imgUrlB, props.data.imgUrlC, props.data.imgUrlD];

    const handlers = useSwipeable({
        onSwipedLeft: () => {
            setFade(true);
            setCurrentImageIndex((currentImageIndex + 1) % images.length);
        },
        onSwipedRight: () => {
            setFade(true);
            setCurrentImageIndex((currentImageIndex - 1 + images.length) % images.length);
        },
        preventDefaultTouchmoveEvent: true,
        trackMouse: true
    });

    useEffect(() => {
        const intervalId = setInterval(() => {
            setFade(true);
            setCurrentImageIndex((currentImageIndex + 1) % images.length);
        }, 13000); // Cambia la imagen cada 4 segundos - 6000
        return () => clearInterval(intervalId); // Limpia el intervalo cuando el componente se desmonta
    }, [currentImageIndex, images.length]);

    useEffect(() => {
        if (fade) {
            const timer = setTimeout(() => {
                setFade(false);
            }, 11000); // Duración de la animación
            return () => clearTimeout(timer);
        }
    }, [fade]);

    useEffect(() => {
        setIsthisPlay(parseInt(props.data.index) === props.trackData.trackKey[0]);
    }, [props.data.index, props.trackData.trackKey]);

    function handleImageLoad(imageUrl) {
        setLoadedImages(prevImages => [...prevImages, imageUrl]);
    }

    function handleError(e) {
        e.target.src = './assets/icons/share.jpg';  /* ./../image/set/selection.jpg */
    }

    function getImage(imageUrl) {
        if (loadedImages.includes(imageUrl)) {
            return imageUrl; // Usar la imagen de la caché
        } else {
            return null; // La imagen aún no está en la caché
        }
    }

    return (
        <div className={styles.PlaylistCardGBox} {...handlers}>
            {/* LINK PLAYLIST */}
            <Link to={`/scroll`}> {/* timeline */}
                <div className={styles.PlaylistCardG}>
                    <div className={styles.ImgBox} style={{ position: 'relative' }}>
                        {!loadedImages.includes(images[currentImageIndex]) &&
                            <ContentLoader
                                speed={8}
                                width={340}
                                height={130}
                                viewBox="0 0 340 130"
                                backgroundColor="rgba(107, 183, 242,.05)"
                                foregroundColor="rgba(107, 183, 242,.15)"
                                style={{ position: 'absolute', width: '100%', height: '100%', top: 0, left: 0 }}
                            >
                                <rect x="0" y="0" rx="8" ry="8" width="340" height="130" />
                            </ContentLoader>}
                        <img
                            className={fade ? 'fade-in' : ''}
                            src={getImage(images[currentImageIndex]) || images[currentImageIndex]}
                            alt={`${props.data.title}`}
                            onError={handleError}
                            onLoad={() => handleImageLoad(images[currentImageIndex])}
                            style={loadedImages.includes(images[currentImageIndex]) ? { position: 'absolute', top: 0, left: 0 } : { display: 'none' }}
                        />
                        <div style={{ position: 'absolute', display: 'flex', bottom: '-1.3rem', left: '0px' }}>
                            {images.map((image, index) => (
                                <div
                                    key={index}
                                    style={{
                                        position: 'relative',
                                        display: 'flex',
                                        height: '10px',
                                        width: '4px',
                                        backgroundColor: index === currentImageIndex ? '#396fbf' : '#555555',
                                        margin: '2px',
                                        borderRadius: '1px'
                                    }}
                                />
                            ))}
                        </div>
                    </div>
                    <div className={styles.Title}>
                        <Marquee speed={20}>
                            <TextBoldL> {props.data.comentario} &nbsp; {props.data.title} – {props.data.artist} &nbsp; </TextBoldL>
                        </Marquee>
                    </div>
                    {<div className={styles.Tag}>
                        <img src={props.data.artistImgUrl} alt={`${props.data.title}`} />
                    </div>}
                </div>
            </Link>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        trackData: state.trackData,
        isPlaying: state.isPlaying
    };
};

export default connect(mapStateToProps, { changeTrack })(PlaylistCardG);
